import React from "react";
import logo from "../../styles/assets/images/Hero-logo.avif";
import { Country } from "country-state-city";
import { useStaticQuery, graphql } from "gatsby";
import { initializeApp } from "firebase/app";
import { collection, addDoc, getFirestore } from "firebase/firestore/lite";
import { utils } from "../../core/helper";

const MoveForward = () => {
  // const data = useStaticQuery(graphql`
  //   query {
  //     site(siteMetadata: {}) {
  //       id
  //       firestore {
  //         apiKey
  //         authDomain
  //         projectId
  //         storageBucket
  //         messagingSenderId
  //         appId
  //       }
  //     }
  //   }
  // `);
  // Initialize Firebase

  const firebaseConfig = {
    apiKey: process.env.FIREBASE_API_KEY,
    authDomain: process.env.FIREBASE_AUTH_DOMAIN,
    projectId: process.env.FIREBASE_PROJECT_ID,
    storageBucket: process.env.FIREBASE_STORAGE_BUCKET,
    messagingSenderId: process.env.FIREBASE_MESSAGE_SENDER_ID,
    appId: process.env.FIREBASE_APP_ID,
  };

  // console.log("data::", data);

  const app = initializeApp(firebaseConfig);
  console.log("app::", app);
  const db = getFirestore(app);

  const countries = Country.getAllCountries();
  const init = {
    name: "",
    email: "",
    country: "",
    contact: "",
    message: "",
  };
  const [talkToUsInp, setTalkToUsInp] = React.useState(init);

  function handleTalkToUsInpChange(event) {
    const { name, value } = event.target;
    console.log("valuesOnchange::", value);
    setTalkToUsInp((prevFormData) => ({ ...prevFormData, [name]: value }));
  }

  const handleSubmit = async (e) => {
    e.preventDefault();
    const clients = collection(db, "clients");
    console.log("clients::", clients);
    await addDoc(clients, talkToUsInp);
    utils.showSuccssMsg("We will contact/talk to you soon");
    setTalkToUsInp(init);
  };

  return (
    <section className="section-cta" id="talktous">
      <div className="container">
        <div className="cta">
          <div className="bottomAnimation cta-text-box">
            <h2 className="heading-secondary">
              READY TO MOVE FORWARD WITH JUSTTRY TEAM?
            </h2>
            <p className="cta-text">
              Increase efficency, productivity, and business performance with
              Flipflop various products that have been used by tens of thousands
              of businesses in the word.
            </p>

            <form onSubmit={handleSubmit} className="cta-form">
              <div>
                <label for="full-name">Name</label>
                <input
                  id="full-name"
                  type="text"
                  placeholder="Enter Your Name"
                  name="name"
                  value={talkToUsInp.name}
                  onChange={(e) => handleTalkToUsInpChange(e)}
                  required
                />
              </div>
              <div>
                <label for="email">Email address</label>
                <input
                  id="email"
                  type="email"
                  placeholder="me@example.com"
                  name="email"
                  value={talkToUsInp.email}
                  onChange={(e) => handleTalkToUsInpChange(e)}
                  required
                />
              </div>
              <div>
                <label for="select-where">Where did you hear from us?</label>
                <select
                  name="country"
                  onChange={(e) => handleTalkToUsInpChange(e)}
                  id="select-where"
                  required
                >
                  <option value="">Please choose one option:</option>
                  {countries.map((ele, ind) => {
                    return (
                      <option key={ind} value={ele.isoCode}>
                        {ele.name}
                      </option>
                    );
                  })}
                </select>
              </div>
              <div>
                <label for="mobile">Skype / Whatsapp / Mobile</label>
                <input
                  id="mobile"
                  type="tel"
                  placeholder="Skype / Whatsapp / Mobile"
                  name="mobile"
                  value={talkToUsInp.mobile}
                  onChange={(e) => handleTalkToUsInpChange(e)}
                  required
                />
              </div>
              <div>
                <label for="content">Content</label>
                <textarea
                  id="content"
                  placeholder="Type content here"
                  name="content"
                  value={talkToUsInp.content}
                  onChange={(e) => handleTalkToUsInpChange(e)}
                  rows={5}
                  className="input"
                  required
                ></textarea>
              </div>
              <button className="btn btn--form">Submit</button>
            </form>
          </div>

          <div
            className="rightAnimation cta-img-box"
            role="img"
            aria-label="Some Tech Design"
          >
            <img src={logo} alt="" />
          </div>
        </div>
      </div>
    </section>
  );
};

export default MoveForward;

